@charset "UTF-8";
.twofas-print-backup-codes {
  width: 100%;
  text-align: center; }
  .twofas-print-backup-codes .twofas-backup-codes-list {
    width: 300px;
    margin: auto;
    display: inline-block;
    text-align: left; }
    .twofas-print-backup-codes .twofas-backup-codes-list ul {
      list-style: none;
      padding: 0 0 10px 0; }
    .twofas-print-backup-codes .twofas-backup-codes-list li {
      font-size: 1.5em; }
      .twofas-print-backup-codes .twofas-backup-codes-list li:before {
        content: '●';
        margin-right: 10px;
        font-size: 18px;
        color: #ff0000; }
    .twofas-print-backup-codes .twofas-backup-codes-list span {
      color: #000;
      font-size: 0.8em; }
  .twofas-print-backup-codes .twofas-print-backup-codes-footer {
