      display: inline-block; }
      .twofas-print-backup-codes .twofas-print-backup-codes-footer .twofas-print-backup-codes-wrapper img,
      .twofas-print-backup-codes .twofas-print-backup-codes-footer .twofas-print-backup-codes-wrapper span {
        display: inline-block;
        vertical-align: middle; }
      .twofas-print-backup-codes .twofas-print-backup-codes-footer .twofas-print-backup-codes-wrapper img {
        margin-right: 10px; }
      .twofas-print-backup-codes .twofas-print-backup-codes-footer .twofas-print-backup-codes-wrapper span {
        font-size: 1.2em; }
